import React from 'react';
import logo from '../media/logo/gabrielb_logo.gif'
import {Button, Navbar} from "flowbite-react";

const NavBar = () => {
    return (
        <div>
            <Navbar
                fluid={true}
                rounded={true}
            >
                <Navbar.Brand href="https://flowbite.com/">
                    <img
                        src={logo}
                        className="mr-3 h-6 sm:h-9"
                        alt="Gabriel Beltran"
                    />
                </Navbar.Brand>
                <Navbar.Toggle />
                <Navbar.Collapse >
                    <Navbar.Link
                        href="/"
                        active={true}
                    >
                        home
                    </Navbar.Link>
                    <Navbar.Link href="/aboutme">
                        about_me
                    </Navbar.Link>
                    <Navbar.Link href="/portfolio">
                        portfolio
                    </Navbar.Link>
                    <Navbar.Link href="/contact">
                        contact
                    </Navbar.Link>
                </Navbar.Collapse>
            </Navbar>
        </div>

)
};

export default NavBar;